import React, {useContext, useEffect} from "react"

import MyContext from "../Contexts/MyContext"
export default function Cart(props){
    var [cart, setCart]  = useContext(MyContext)

    return(
        <div>
            <div className="orders-container">
                {
                    cart.map(order =>
                        <div className="order-container dynamic-bg">
                            <div>
                                <h3>{order.item.name}</h3>
                                <img src = {order.item.href} width = "100" height = "100"/>
                            </div>

                            <div>
                                <p>Price: ${order.item.price} per {order.item.unit}</p>
                                <p>Quantity: {order.quantity}</p>
                            </div>
                        </div>)
                }
            </div>
            <button onClick={() => alert("Thanks for buying!")}>Buy</button>
        </div>
    )
}